	module.exports = {"data":{"markdownRemark":{"html":"<p>Do you remember custom directives from the shaping up with angular course? You can also make a custom directive!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>>> yo angular-fullstack:directive newdirective</code></pre>\n      </div>\n<p>And if you need an html template for your custom directive (maybe you’re just making a directive to clean up your HTML code), tell it to make an html file when it prompts you to and you’ll be able to include the contents of that template anywhere in your app with:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><newdirective></newdirective></code></pre>\n      </div>","fields":{"slug":"/miscellaneous/creating-a-new-directive-in-angular/"},"frontmatter":{"title":"Creating a New Directive in Angular"}}},"pathContext":{"slug":"/miscellaneous/creating-a-new-directive-in-angular/"}}

